import React from "react";
import Web3 from "web3";
/*import Counter from 'react-number-counter';*/
import {  ABI, ADDRESS } from "../../LuckyContractConfig";
import classNames from 'classnames';
import { useTranslation } from "react-i18next";

class SmartContractLoader extends React.Component{
  static dataFetched = false;

  async loadBlockchainData() {
    try {
      const web3 = new Web3(Web3.givenProvider || "http://localhost:8545");
      const contract = new web3.eth.Contract(ABI, ADDRESS);
      const account = contract.defaultAccount;
      await contract.methods
        .decimals()
        .call()
        .then((value) => {
          this.setState({ decimals: value });
        });
      await contract.methods
        .luckyTotal()
        .call()
        .then((value) => {
          this.setState({ totalTokensDistributedOnTransactions: value });
        });
      await contract.methods
        .burnTotal()
        .call()
        .then((value) => {
          this.setState({ totalBurnt: value });
        });
      await contract.methods
        .jackpotTotal()
        .call()
        .then((value) => {
          this.setState({ actualJackpot: value });
        });
      await contract.methods
        .totalSupply()
        .call()
        .then((value) => {
          this.setState({ totalSupply: value });
        });
      this.formatNumerics();
      this.dataFetched = true;
    } catch (error) {
      console.log(error);
    }
  }

  componentDidMount() {
    this.loadBlockchainData();
    this.render();
  }
  formatNumerics() {
    const {
      totalSupply,
      actualJackpot,
      totalBurnt,
      totalTokensDistributedOnTransactions,
      decimals,
    } = this.state;
    this.setState({ totalSupply: Math.round(totalSupply / 10 ** decimals) });
    this.setState({ actualJackpot:  Math.round(actualJackpot / 10 ** decimals) });
    this.setState({ totalBurnt:  Math.round(totalBurnt / 10 ** decimals) });
    this.setState({
      totalTokensDistributedOnTransactions:
      Math.round(totalTokensDistributedOnTransactions / 10 ** decimals),
    });
  }

  constructor(props) {
    super(props);
    this.state = {
      account: "",
      decimals: 1,
      totalSupply: 0,
      totalBurnt: 0,
      actualJackpot: 0,
      totalTokensDistributedOnTransactions: 0,
    };
  }

  render() {

    const outerClasses = classNames(
      'features-tiles section'
    );
  
    const innerClasses = classNames(
      'features-tiles-inner section-inner pt-0',

    );
  
    const tilesClasses = classNames(
      'tiles-wrap center-content'
    );
  
    const {
      totalSupply,
      actualJackpot,
      totalBurnt,
      totalTokensDistributedOnTransactions,
    } = this.state;

    const t = this.props.t;
    return (
      <section className={outerClasses}>
          <div className={innerClasses}>
            <div className={tilesClasses}>
              <div className="tiles-item reveal-from-bottom">
                <div className="tiles-item-inner">
                  <div className="features-tiles-item-content">
                    <h4 className="mt-0 mb-8">{totalSupply}<small>T</small></h4>
                    <p className="m-0 text-sm state-desc">
                      {t("Stats.section.supply")}
                    </p>
                  </div>
                </div>
              </div>
              <div
                className="tiles-item reveal-from-bottom"
                data-reveal-delay="200"
              >
                <div className="tiles-item-inner">
                  <div className="features-tiles-item-content">
                    <h4 className="mt-0 mb-8">{actualJackpot}<small>T</small></h4>
                    <p className="m-0 text-sm state-desc">
                      {t("Stats.section.jackpot")}
                    </p>
                  </div>
                </div>
              </div>
              <div className="tiles-item reveal-from-bottom">
                <div className="tiles-item-inner">
                  <div className="features-tiles-item-content">
                    <h4 className="mt-0 mb-8">{totalBurnt}<small>T</small></h4>
                    <p className="m-0 text-sm state-desc">
                      {t("Stats.section.burnt")}
                    </p>
                  </div>
                </div>
              </div>
              <div className="tiles-item reveal-from-bottom">
                <div className="tiles-item-inner">
                  <div className="features-tiles-item-content">
                    <h4 className="mt-0 mb-8">{totalTokensDistributedOnTransactions}<small>T</small></h4>
                    <p className="m-0 text-sm state-desc">
                      {t("Stats.section.distributed")}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
      </section>
    );
  }
}
export default SmartContractLoader;
