import React from "react";
import classNames from "classnames";
import { SectionTilesProps } from "../../utils/SectionProps";
import SectionHeader from "./partials/SectionHeader";
import Image from "../elements/Image";
import { useTranslation } from "react-i18next";
const propTypes = {
  ...SectionTilesProps.types,
};

const defaultProps = {
  ...SectionTilesProps.defaults,
};
const Team = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {
  const [t] = useTranslation("common");
  const outerClasses = classNames(
    "features-tiles section",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "features-tiles-inner section-inner pt-0",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );

  const tilesClasses = classNames(
    "tiles-wrap center-content",
    pushLeft && "push-left"
  );

  const sectionHeader = {
    title: t("Team.section.title"),
  };

  return (
    <section {...props} className={outerClasses}>
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={tilesClasses}>
            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image-team mb-16">
                    <Image
                      src={require("./../../assets/images/avatars/al.png")}
                      alt="al"
                      width={180}
                      height={180}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">{t("Team.tile.1.title")}</h4>
                  <p className="m-0 text-sm">{t("Team.tile.1.description")}</p>
                </div>
              </div>
            </div>
            <div
              className="tiles-item reveal-from-bottom"
              data-reveal-delay="200"
            >
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image-team mb-16">
                    <Image
                      src={require("./../../assets/images/avatars/denzel.png")}
                      alt="denzel"
                      width={180}
                      height={180}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">{t("Team.tile.2.title")}</h4>
                  <p className="m-0 text-sm">{t("Team.tile.2.description")}</p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image-team mb-16">
                    <Image
                      src={require("./../../assets/images/avatars/ladouille.png")}
                      alt="ladouille"
                      width={180}
                      height={180}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">{t("Team.tile.3.title")}</h4>
                  <p className="m-0 text-sm">{t("Team.tile.3.description")}</p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image-team mb-16">
                    <Image
                      src={require("./../../assets/images/avatars/hurricane.png")}
                      alt="hurricane"
                      width={180}
                      height={180}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">{t("Team.tile.4.title")}</h4>
                  <p className="m-0 text-sm">{t("Team.tile.4.description")}</p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image-team mb-16">
                    <Image
                      src={require("./../../assets/images/avatars/safe.png")}
                      alt="safe"
                      width={180}
                      height={180}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">{t("Team.tile.5.title")}</h4>
                  <p className="m-0 text-sm">{t("Team.tile.5.description")}</p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image-team mb-16">
                    <Image
                      src={require("./../../assets/images/avatars/yopilou.png")}
                      alt="yopilou"
                      width={180}
                      height={180}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">{t("Team.tile.6.title")}</h4>
                  <p className="m-0 text-sm">{t("Team.tile.6.description")}</p>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
    </section>
  );
};

Team.propTypes = propTypes;
Team.defaultProps = defaultProps;

export default Team;
