import React from "react";
// import sections
import Hero from "../components/sections/Hero";
import About from "../components/sections/About";
import Tokenomics from "../components/sections/Tokenomics";
import Functionnalities from "../components/sections/Functionnalities";
import Stats from "../components/sections/Stats";
import Countdown from "../components/sections/Countdown";
import Faq from "../components/sections/Faq";
import Team from "../components/sections/Team";
import Roadmap from "../components/sections/Roadmap";
import ParticlesEffect from "../components/effects/ParticlesEffect";
import Chart from "../components/elements/Chart";
const Home = () => {
  return (
    <>
      <ParticlesEffect/>
      <Countdown id="countdown" time="25" />
      <Hero  className="illustration-section-clover-right" />
      {/*<Chart></Chart>*/}
      <Stats id="stats"/>
      <About id="about" />
      <Tokenomics
        id="tokenomics"
        invertMobile
        topDivider
        /*imageFill*/ className="illustration-section-clover-left"
      />
      <Functionnalities id="functionnalities" topDivider />
      <Team id="team" topDivider />
      <Roadmap id="roadmap" className="section" topDivider />
      <Faq id="faq" topDivider/>
    </>
  );
};

export default Home;
