import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { useTranslation } from 'react-i18next'

const useStyles = makeStyles((theme) => ({
  button: {
    display: 'block',
    marginTop: theme.spacing(2),
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

export default function LanguageSelector() {
  const classes = useStyles();
  const [lang, setLang] = React.useState('');
  const [open, setOpen] = React.useState(false);
  const [t, i18n] = useTranslation('common');

  const handleChange = (event) => {
    setLang(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel id="controlled-open-select-label">{t("Language.label")}</InputLabel>
        <Select
          labelId="controlled-open-select-label"
          id="controlled-open-select"
          open={open}
          onClose={handleClose}
          onOpen={handleOpen}
          value={lang}
          onChange={handleChange}
        >
          <MenuItem  onClick={() => i18n.changeLanguage('fr')} value={t("Language.FR")}>{t("Language.FR")}</MenuItem>
          <MenuItem onClick={() => i18n.changeLanguage('en')} value={t("Language.EN")}>{t("Language.EN")}</MenuItem>
        </Select>
      </FormControl>
    </div>
  );
}