import React from "react";
import Particles from "react-particles-js";

const ParticlesEffect = () => {
  return (
    <Particles
      params={{
        particles: {
          number: {
            value: 10,
            density: {
              enable: true,
              value_area: 800,
            },
          },
          line_linked: {
            enable: false,
          },
          move: {
            speed: 1,
            out_mode: "out",
          },
          shape: {
            type: ["image"],
            image: [
              {
                src: "/clover.png",
                height: 25,
                width: 25,
              },
            ],
          },
          size: {
            value: 20,
            random: false,
            anim: {
              enable: true,
              speed: 4,
              size_min: 10,
              sync: false,
            },
          },
          rotate: {
            random: true,
            value: 50,
            animation: {
              enable: true,
              speed: 100,
            },
          },
        },
        fpsLimit: 60,
        retina_detect: false,
      }}
    />
  );
};

export default ParticlesEffect;
